.account-head {
    position: relative;
    margin-top: 20px;
}

.banner-profile {
    position: absolute;
    top: 35%;
    left: 50px;
    h2 {
        font-size: 47px;
        color: #ffffff;
        font-weight: bold;
        text-transform: none;
    }
    span {
        color: #ffffff;
        font-size: 15px;
        a {
            color: $dia-light;
        }
    }
}

.myaccount-layout-row {
    width: 98%;
    margin: -65px auto 0;
}

.n-service {
    background: $dia-light;
    padding: 15px;
    h4 {
        line-height: 1;
        color: #ffffff;
        font-weight: bold;
        text-transform: none;
        font-size: 22px;
        margin-bottom: 8px;
    }
    p {
        color: #ffffff;
        font-size: 16px;
        line-height: 1.4;
    }
    h5 {
        color: #ffffff;
        text-transform: none;
        font-weight: 600;
        font-size: 16px;
        margin-top: 20px;
    }
    a {
        color: #fff;
        font-size: 16px;
        &:hover {
            text-decoration: underline;
        }
    }
}

.account-manager {
    margin-top: 20px;
    background: $dia-blue;
    padding: 15px;
    .manager {
        text-align: center;
        padding-bottom: 15px;
        border-bottom: 1px solid #ffffff;
        padding-top: 15px;
        &:last-child {
            border: 0;
        }
        p {
            font-weight: 400;
            color: #ffffff;
            font-size: 14px;
            a {
                color: #ffffff;
                font-size: 14px;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
    .position {
        color: #ffffff;
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 15px;
    }
    .details {
        display: flex;
        display: -webkit-flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 7px;
        img {
            width: 22px;
            margin-right: 6px;
        }
    }
}

.manager>img {
    width: 125px;
    margin: 0 auto;
    display: table;
}

.acc-boxes {
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: space-around;
    column-gap: 15px;
    .box-in {
        width: 25%;
        min-height: 185px;
        .box-up {
            background: #00426a;
            padding: 10px;
            color: #ffffff;
            font-size: 17px;
            display: flex;
            align-items: center;
            padding: 20px 5px;
            img {
                width: 45px;
                margin-right: 2px;
                height: 35px;
                object-fit: contain;
            }
        }
        .box-down {
            text-align: right;
            padding: 15px 15px 15px;
            border-width: 0 2px 2px 2px;
            border-color: #ccc;
            border-style: solid;
            border-radius: 0 0 10px 10px;
            .count {
                font-size: 40px;
                font-weight: 600;
                line-height: 1;
            }
            a {
                font-size: 15px;
            }
        }
    }
}

.start-shop {
    border: 2px solid $dia-blue;
    padding: 8px;
    border-radius: 10px;
    text-align: center;
    margin-top: 15px;
    margin-bottom: 25px;
    a {
        font-size: 23px;
        font-weight: 600;
        color: $dia-blue;
        &:hover {
            color: $dia-blue;
            text-decoration: underline;
        }
    }
}

.solution-banner {
    background: aqua;
    background-image: url(https://www.campcushion.com/core/media/media.nl?id=25476&c=4420736_SB1&h=RIwThiWv2FPBP5phvKfNdL5OAE-jHvLtLeDGw-YVEZXUPCTs&fcts=20220517083334&whence=);
    padding: 45px 30px;
    background-size: contain;
    margin: 10px 0 25px;
    .banner-con {
        h3 {
            color: #ffffff;
            font-size: 25px;
            font-weight: 600;
            text-transform: none;
            margin-bottom: 10px;
        }
        p {
            font-size: 18px;
            color: #ffffff;
            line-height: 1.3;
            max-width: 88%;
        }
        a {
            background: #ffffff;
            margin-top: 20px;
            font-weight: 600;
        }
    }
}

.table-order {
    margin-bottom: 25px;
    table {
        border-collapse: collapse;
        width: 100%;
        th {
            background: #e2e2e2;
            font-size: 18px;
            font-weight: 600;
            padding: 10px 15px;
            border: 1px solid #ccc;
            color: #000;
            &:nth-child(even) {
                background: #d3d3d3;
            }
        }
        td {
            border: 1px solid #ccc;
            padding: 10px 15px;
            font-size: 16px;
        }
    }
}